html, body,
h1, h2, h3, h4, h5, h6,
a, p, span,
em, small, strong, sub, sup,
mark, del, ins, strike, abbr, dfn,
blockquote, q, cite,
code, pre,
ol, ul, li, dl, dt, dd,
div, hr, section, article, main, aside, nav, header, hgroup, footer,
img, figure, figcaption,
address, time, audio, video, canvas, iframe,
details, summary, fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td {
	margin : 0;
	padding: 0;
	border : 0;
}


// Layout ----------------------------------------------------------------------


html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing       : inherit;
	background-repeat: no-repeat;
}

main {
	display: block;  // for IE11
}


// Typography ------------------------------------------------------------------


html {
	line-height             : 1;
	-webkit-text-size-adjust: 100%;  // for iOS

	-moz-tab-size: 4;
	     tab-size: 4;
}

* {
	font-size  : inherit;
	font-family: inherit;
	line-height: inherit;
}

a,
a:visited {
	color          : inherit;
	text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
	font-size  : 100%;
	font-weight: normal;
}

mark {
	color           : inherit;
	background-color: inherit;
}

address {
	font-style: inherit;
}

code, kbd, samp, pre {
	font-size: 1em;
}


// Element ---------------------------------------------------------------------


ol[class],
ul[class] {
	list-style: none;
}

table {
	border-collapse: collapse;
	border-spacing : 0;
}

td, th {
	padding   : 0;
	text-align: left;
}

img, iframe, embed, object, audio, video {
	max-width: 100%;
	height   : auto;
	border   : none;
}


// Form ------------------------------------------------------------------------


button,
input,
select,
textarea {
	border-radius: 0;
}

button,
[type="button"],
[type="reset"],
[type="submit"],
[role="button"] {
	cursor: pointer;  // Apply cursor pointer to button elements
}

[disabled] {
	cursor: default;  // Replace pointer cursor in disabled elements
}

button,
input,
optgroup,
select,
textarea {
	font  : inherit;  // Specify font inheritance of form elements
	margin: 0;  // Remove the margin on controls in Chrome and Safari
}

button,
input,
select,
textarea {
	// Remove the default button styling in all browsers
	border: 1px solid WindowFrame;
	color : inherit;
}

button,
input {
	line-height: 1;
	overflow   : visible;  // Show the overflow in Edge and IE
}

button,
select {
	text-transform: none;  // Remove the inheritance of text transform in Firefox.
}

button,
[type='button'],
[type='reset'],
[type='submit'] {
	-webkit-appearance: button; //  Correct the inability to style buttons in iOS and Safari

	&::-moz-focus-inner {
		// Remove the inner border and padding of focus outlines in Firefox
		border-style: none;
		padding     : 0;
	}
	&:-moz-focusring {
		// Restore the focus outline styles unset by the previous rule in Firefox
		outline: 1px dotted ButtonText;
	}
}

[type='search'] {
	-webkit-appearance: none;  // Correct the odd appearance in Safari on iOS
	outline-offset    : -2px;  // Correct the outline style in Safari

	&::-webkit-search-decoration {
		-webkit-appearance: none;  // Remove the inner padding in Chrome and Safari on macOS
	}
}

[type='number'] {
	width: auto;  // Firefox 36+
}

[type='range'] {
	padding: 0;  // IE 11
}

select {
	-webkit-appearance: none;  // Chrome 41+
	   -moz-appearance: none;  // Firefox 36+

	&::-ms-expand {
		display: none;  // IE 11
	}
	&::-ms-value {
		color: currentColor;  // IE 11
	}
}

textarea {
	overflow: auto;      // IE 11
	resize  : vertical;  // Specify textarea resizability
}

legend {
	border     : 0;        // Correct `color` not being inherited in IE 8/9/10/11
	color      : inherit;  // Correct the color inheritance from `fieldset` elements in IE
	display    : table;    // Correct the text wrapping in Edge and IE
	max-width  : 100%;     // Correct the text wrapping in Edge and IE
	white-space: normal;   // Correct the text wrapping in Edge and IE
}

progress {
	display       : inline-block;  // Add the correct display in Edge 18- and IE.
	vertical-align: baseline;      // Add the correct vertical alignment in Chrome, Edge, and Firefox.
}

:-ms-input-placeholder {
	color  : inherit;
	opacity: 0.54;
}

::-webkit-file-upload-button {
	-webkit-appearance: button;   // Correct the inability to style clickable types in iOS and Safari
	font              : inherit;  // Change font properties to `inherit` in Chrome and Safari
}
